import { createContext, useState, useEffect, ReactNode, useContext } from "react";
import axios, { AxiosError } from "axios";
import Cookies from "js-cookie";
import { useRouter } from "next/router";

// User type interface
interface User {
  id: string;
  username: string;
  email: string;
}

interface AuthContextType {
  user: User | null;
  login: (identifier: string, password: string) => Promise<void>;
  logout: () => void;
}

// Create AuthContext
export const AuthContext = createContext<AuthContextType | null>(null);

// AuthProvider component
export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);
  const router = useRouter();
  useEffect(() => {
    const token = Cookies.get("token");
    if (token) {
      axios
        .get("http://localhost:8000/user", {
          headers: { Authorization: `Bearer ${token}` },
        })
        .then((res) => {
          console.log("✅ User Data:", res.data);
          setUser(res.data);
        })
        .catch((error) => {
          console.error("❌ Failed to fetch user:", error.response?.data || error.message);
          Cookies.remove("token");
          setUser(null);
        });
    }
  }, []);
  const login = async (identifier: string, password: string) => {
    try {
      const formData = new FormData();
      formData.append("username", identifier);
      formData.append("password", password);
  
      const res = await axios.post("http://localhost:8000/token", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
  
      const token = res.data.access_token;
      Cookies.set("token", token, { expires: 7 }); // Store token for 7 days
  
      // ✅ Fetch user immediately after login
      const userRes = await axios.get("http://localhost:8000/user", {
        headers: { Authorization: `Bearer ${token}` },
      });
  
      setUser(userRes.data); // ✅ Set user correctly in state
      console.log("🔓 Logged in successfully!", userRes.data);
      router.push("/");
    } catch (error) {
      const axiosError = error as AxiosError;
      console.error("❌ Login Error:", axiosError.response?.data || axiosError.message);
      alert("Invalid credentials. Please try again.");
    }
  };

  const logout = () => {
    Cookies.remove("token");
    setUser(null);
    router.push("/login");
  };

  return (
    <AuthContext.Provider value={{ user, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

// Custom hook for easier access to AuthContext
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};
